</template>


<script>
export default {
  methods: {
    goToMenu() {
      // 
      // this.$router.go(-1);
      // 
      // this.$router.replace("/menu");
      // 
      // this.$router.replace({ name: "menuLink" });

      // this.$router.push("/about");
      this.$router.push({ name: "aboutLink" });
    },
  },
};
</script>

<style scoped>
#home {
  background: url("../../src/assets/pizza.jpg");
  background-position: center;
  height: 85vh;
  padding: 10%;
}
h1,
h2 {
  margin: 6%;
}

.background {
  background-color: #eee;
  opacity: 0.8;
  max-width: 70vw;
  margin: 0 auto;
  padding: 20px 0;
}
</style>


