<template>
  <div class="row">
    <div class="col-sm-12 col-md-8">
      <!--  -->
      <NewPizza />
    </div>
    <div class="col-sm-12 col-md-4">
      <!--  -->
      <h3 class="text-muted my-5">:</h3>
      <table class="table">
        <thead class="table table-default">
          <tr>
            <th></th>
            <th></th>
          </tr>
        </thead>
        <tbody v-for="item in getMenuItems" :key="item.name">
          <tr>
            <td>{{item.name}}</td>
            <td>
              <button @click="deleteData(item)" class="btn btn-outline-danger btn-sm">x</button>
            </td>
          </tr>
        </tbody>
      </table>
    </div>
  </div>
</template>

<script>
import NewPizza from "./NewPizza";
export default {
  data() {
    return {
      // getMenuItems: [],
    };
  },
  components: {
    NewPizza,
  },
  // 
  created() {
    fetch("https://order-72eca-default-rtdb.firebaseio.com/menu.json")
      .then((res) => {
        return res.json();
      })
      .then((data) => {
        let menuArray = [];
        for (let key in data) {
          data[key].id = key;
          menuArray.push(data[key]);
        }
        // this.getMenuItems = menuArray;

        // vuex
        this.$store.commit("setMenuItems", menuArray);
      });
  },
  computed: {
    getMenuItems: {
      // Vuex
      get() {
        return this.$store.state.menuItems;
      },
      set() {},
    },
  },
  methods: {
    deleteData(item) {
      fetch(
        "https://order-72eca-default-rtdb.firebaseio.com/menu/" +
          item.id +
          "/.json",
        {
          method: "DELETE",
          headers: { "Content-type": "application/json" },
        }
      )
        .then((res) => {
          res.json();
        })
        .then((data) => {
          // 
          // this.$router.push({ name: "menuLink" });
          this.$store.commit("removeMenuItems", item);
        })
        .catch((err) => {
          console.log(err);
        });
    },
  },
  //  this
  // beforeRouteEnter: (to, from, next) => {
  //   // next this
  //   next((vm) => {
  //     alert("hello " + vm.name);
  //   });
  // },
  // // this
  // beforeRouteLeave: (to, from, next) => {
  //   if (confirm("?")) {
  //     next();
  //   } else {
  //     next(false);
  //   }
  // },
};
</script>


