<template>
  <div class="row">
    <!--  -->
    <div class="col-sm-12 col-md-8">
      <table class="table">
        <thead class="thead-default">
          <tr>
            <th></th>
            <th></th>
            <th></th>
          </tr>
        </thead>
        <tbody v-for="item in getMenuItems" :key="item.name">
          <tr>
            <td>
              <strong>{{item.name}}</strong>
            </td>
          </tr>
          <tr v-for="option in item.options" :key="option.size">
            <td>{{option.size}}</td>
            <td>{{option.price}}</td>
            <td>
              <button @click="addToBasket(item,option)" class="btn btn-sm btn-outline-success">+</button>
            </td>
          </tr>
        </tbody>
      </table>
    </div>
    <!--  -->
    <div class="col-sm-12 col-md-4">
      <div v-if="baskets.length>0">
        <table class="table">
          <thead class="thead-default">
            <tr>
              <th></th>
              <th></th>
              <th></th>
            </tr>
          </thead>
          <tbody v-for="item in baskets" :key="item.name">
            <tr>
              <td>
                <button @click="decreaseQuantity(item)" class="btn btn-sm">-</button>
                <span>{{item.quantity}}</span>
                <button @click="increaseQuantity(item)" class="btn btn-sm">+</button>
              </td>
              <td>{{item.name}}{{item.size}}</td>
              <td>{{item.price*item.quantity}}</td>
            </tr>
          </tbody>
        </table>
        <p>:{{total+"RMB"}}</p>
        <button class="btn btn-success btn-block"></button>
      </div>
      <div v-else>{{basketsText}}</div>
    </div>
  </div>
</template>
<script>
import axios from "axios";
export default {
  data() {
    return {
      baskets: [],
      basketsText: "",
      // getMenuItems: {},
    };
  },
  computed: {
    getMenuItems() {
      // Vuex()
      // return this.$store.state.menuItems;
      // getters
      return this.$store.getters.getMenuItems;
    },
    total() {
      let totalCost = 0;
      for (let index in this.baskets) {
        let individualItem = this.baskets[index];
        totalCost += individualItem.quantity * individualItem.price;
      }
      return totalCost;
    },
  },
  created() {
    this.fetchData();
  },
  methods: {
    fetchData() {
      // fetch
      // fetch("https://order-72eca-default-rtdb.firebaseio.com/menu.json")
      //   .then((res) => {
      //     return res.json();
      //   })
      //   .then((data) => {
      //     this.getMenuItems = data;
      //   });
      // axios
      // axios
      //   .get("https://order-72eca-default-rtdb.firebaseio.com/menu.json")
      //   .then((res) => {
      //     this.getMenuItems = res.data;
      //   })
      //   .catch((err) => {
      //     console.log(err);
      //   });
      // Vue
      // this.http
      //   .get("menu.json")
      //   .then((res) => {
      //     this.getMenuItems = res.data;
      //   })
      //   .catch((err) => {
      //     console.log(err);
      //   });
      // vuex
      this.http
        .get("menu.json")
        .then((res) => {
          this.$store.commit("setMenuItems", res.data);
        })
        .catch((err) => {
          console.log(err);
        });
    },
    addToBasket(item, option) {
      const basket = {
        name: item.name,
        size: option.size,
        price: option.price,
        quantity: 1,
      };
      if (this.baskets.length > 0) {
        let result = this.baskets.filter((basket) => {
          return basket.name === item.name && basket.price === option.price;
        });
        if (result !== null && result.length > 0) {
          result[0].quantity++;
        } else {
          this.baskets.push(basket);
        }
      } else {
        this.baskets.push(basket);
      }
    },
    decreaseQuantity(item) {
      item.quantity--;
      if (item.quantity <= 0) {
        this.removeFromBaskets(item);
      }
    },
    increaseQuantity(item) {
      item.quantity++;
    },
    removeFromBaskets(item) {
      this.baskets.splice(this.baskets.indexOf(item), 1);
    },
  },
};
</script>


